<template>
  <div class="header">
    <el-row>
      <el-col :span="12">
        <div>
          <el-breadcrumb
            separator-class="el-icon-arrow-right"
            v-if="this.$route.path != '/'"
          >
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>{{ parentName }}</el-breadcrumb-item>
            <el-breadcrumb-item>{{ name }}</el-breadcrumb-item>
          </el-breadcrumb>
          <el-breadcrumb separator-class="el-icon-arrow-right" v-else>
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
      </el-col>
      <el-col :span="12">
        <div style="text-align: right">
          <!-- <span class="headerIcon logoImg"> </span> -->
          <!-- <span class="headerIcon"> 无界工场(上海)软件科技有限公司 </span> -->
          <!-- <span class="headerIcon"> 无界工场(上海)软件科技有限公司 </span> -->
          <el-dropdown trigger="click">
            <span class="username">
              <i class="iconfont icon-peoples" style="padding-right: 10px"></i>
              <span>欢迎您，{{ CurrentUserInfo.user_name }}</span>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <span class="dropdownSpan">修改密码</span>
              </el-dropdown-item>
              <el-dropdown-item>
                <span class="dropdownSpan" @click="logout"
                  >注销</span
                ></el-dropdown-item
              >
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "CommonHeader",
  computed: {
    ...mapState(["CurrentUserInfo"]),
  },
  data() {
    return {
      breadList: [],
      parentName: "",
      name: "",
    };
  },
  watch: {
    $route() {
      this.getBreadcrumb();
    },
  },
  methods: {
    logout() {
      sessionStorage.clear();
      this.$store.replaceState(Object.assign({}, this.$store.state, JSON.parse(
        JSON.stringify({
          // 登录人员信息
          CurrentUserInfo: {},
          // 不分页的物料详情表接口
          NoPagePersonMatterData: [],
          // 产品名称和内码接口
          ProductCodeNameList: [],
          // 产品名称和编码接口
          ProductIdNameList: [],
          // 产品 物料 名称和编码接口
          BomMatterCodeNameList: [],
          // 工站内码和工站名称接口：
          WorkCodeNameList: [],
          WorkCodeNameSearchList: [],
          // 人员内码和人员名称
          PersonCodeNameList: [],
          // 生产计划内码和名称接口：
          ProductPlanCodeNameList: [],
          // 物料内码和名称接口：
          MatterCodeNameList: [],
          // 工序内码和名称接口：
          ProcessCodeNameList: [],
        })
      )));


      if (this.$route.fullPath.indexOf("userUrl") > 0) {
        this.$router.push({ path: "/UserLogin" });
      } else {
        this.$router.push({ path: "/ManagerLogin" });
      }
      // this.
    },
    getBreadcrumb() {
      this.parentName = this.$route.meta.parentName;
      this.name = this.$route.meta.name;
      console.log("CommonHeader  CurrentUserInfo ", this.CurrentUserInfo);
    },
  },
  mounted() {
    // console.log(this.$route.path);
    // console.log(this.$route.meta);
    this.getBreadcrumb();
  },
};
</script>

<style scoped lang="scss">
.header {
  line-height: 60px;

  border-bottom: 1px solid #ebebeb;
  // font-size: 16px;
  .el-breadcrumb {
    line-height: 60px;
  }
  .headerIcon {
    display: inline-block;
    padding-right: 20px;
  }
  .logoImg {
    width: 40px;
    height: 40px;
    background: url("../assets/images/logoImg.png") 100%;
    background-size: cover;
    vertical-align: middle;
  }
  .username {
    height: 100%;
    display: inline-block;
    cursor: pointer;
  }
}

.dropdownSpan {
  display: inline-block;
  width: 100%;
}
</style>
