import axios from 'axios'
import qs from 'qs'
import { Loading } from 'element-ui'
// console.log(1233213213)
// console.log(qs.stringify(params))
let loadings
axios.defaults.timeout = 3000 // 3s没响应则认为该请求失败
// axios.defaults.baseURL = 'http://10.0.20.103:8000/logistic' //本地
axios.defaults.baseURL = 'http://10.0.0.235:8000/logistic' //服务器
axios.defaults.withCredentials = true

// http request 拦截器 所有请求发出前都需要执行以下代码
axios.interceptors.request.use(request => {
  // request.params = qs.stringify(request.params)
  // request.data = qs.stringify(request.data)
  // console.log('request', request)
  console.log('request.params', request.params)
  console.log('request.data', request.data)
  if (request.params) {
    loadings = Loading.service()
  }
  return request
},
  error => {
    return Promise.reject(error)
  }
)

axios.interceptors.response.use(
  request => {
    if (loadings) {
      loadings.close()
    }
    return request
  },
  error => {
    if (loadings) {
      loadings.close()
    }
    return Promise.reject(error)
  }
)

/**
 * get方法，对应get请求
 * @param {String} url [请求的url地址]  
 * @param {Object} params [请求时携带的参数]
 */
export function get(url, params) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err.data)
    })
  })
}

