<template>
  <div
    style="
      height: 100%;
      background-color: #fff;
      border-right: 1px solid #ebebeb;
    "
  >
    <div class="logo">
      <!-- <router-link to="/"><div class="logoImg"></div></router-link> -->
      <router-link to="/"
        ><div class="logoIcon theme-primary-color">
          <i class="iconfont icon-zhuizong"></i> iMotionMTS
        </div></router-link
      >
    </div>

    <el-menu
      class="el-menu-vertical-demo"
      text-color="#606266"
      :default-active="this.$route.path"
      router
    >
      <div v-for="(item, index) in asideMenu" :key="index">
        <el-menu-item
          :index="item.path ? item.path : index + ' only'"
          v-if="!item.children"
        >
          <i :class="'el-icon-' + item.icon"></i>
          <span>{{ item.label }}</span>
        </el-menu-item>

        <el-submenu :index="item.path ? item.path : index + ' only'" v-else>
          <template slot="title">
            <i :class="'el-icon-' + item.icon"></i>
            <span>{{ item.label }}</span>
          </template>
          <el-menu-item
            :index="subItem.path ? subItem.path : index + ' only'"
            v-for="(subItem, subIndex) in item.children"
            :key="subIndex + 'sub'"
          >
            <span>{{ subItem.label }}</span>
          </el-menu-item>
        </el-submenu>
      </div>
    </el-menu>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "CommonAside",
  computed: {
    noChildren() {
      return this.asideMenu.filter((item) => !item.children);
    },
    hasChildren() {
      return this.asideMenu.filter((item) => item.children);
    },
    ...mapState(["CurrentUserInfo"]),
  },
  data() {
    return {
      asideMenu: [],
    };
  },
  methods: {
    ...mapActions(["getLogisticCurrentUserInfo"]),
  },
  mounted() {
    this.asideMenu = [];
    let MissStationArr = [];
    if (this.CurrentUserInfo.work_type == "加工") {
      MissStationArr = [
        {
          path: "/userUrl/MissStation",
          label: "产品过站",
          icon: "document",
        },
        {
          path: "/userUrl/MissStationSearch",
          label: "过站查询",
          icon: "document",
        },
        {
          path: "/userUrl/BadProduct",
          label: "不良产品",
          icon: "document",
        },
      ];
    } else if (this.CurrentUserInfo.work_type == "维修") {
      MissStationArr = [
        {
          path: "/userUrl/MissStationM",
          label: "产品过站",
          icon: "document",
        },
      ];
    } else if (this.CurrentUserInfo.work_type == "包装") {
      MissStationArr = [
        {
          path: "/userUrl/MissStationPack",
          label: "产品过站",
          icon: "document",
        },
        {
          path: "/userUrl/MissStationPackSearch",
          label: "成品列表",
          icon: "document",
        },
      ];
    } else if (this.CurrentUserInfo.work_type == "检验") {
      MissStationArr = [
        {
          path: "/userUrl/MissStationIPQC",
          label: "产品过站",
          icon: "document",
        },
      ];
    } else if (this.CurrentUserInfo.work_type == "入库出库") {
      MissStationArr = [
        {
          path: "/userUrl/FinishedProductStorage",
          label: "成品入库",
          icon: "document",
        },
        {
          path: "/userUrl/FinishedProductDispatch",
          label: "成品出库",
          icon: "document",
        },
      ];
    }

    if (this.CurrentUserInfo.product_id == "db_common") {
      this.asideMenu = [
        {
          path: "/UserList",
          label: "基础管理",
          icon: "document",
          children: [
            {
              path: "/ProjectBOM",
              label: "产品结构",
              icon: "document",
            },
            {
              path: "/UserList",
              label: "人员列表",
              icon: "document",
            },
          ],
        },
        {
          path: "/WorkStationSearch",
          label: "信息追溯",
          icon: "document",
          children: [
            {
              path: "/WorkStationSearch",
              label: "过站查询",
              icon: "document",
            },
          ],
        },
      ];
    } else {
      if (this.$route.fullPath.indexOf("userUrl") > 0) {
        this.asideMenu = [
          {
            path: "/userUrl/UserList",
            label: "基础管理",
            icon: "document",
            children: [
              {
                path: "/userUrl/UserList",
                label: "人员列表",
                icon: "document",
              },
            ],
          },
          {
            path: "/userUrl/MissStation",
            label: "车间管理",
            icon: "document",
            children: MissStationArr,
          },
          {
            path: "/userUrl/MatterProductSearch",
            label: "信息追溯",
            icon: "document",
            children: [
              // {
              //   path: "/userUrl/WorkStationSearch",
              //   label: "过站查询",
              //   icon: "document",
              // },
              {
                path: "/userUrl/MatterProductSearch",
                label: "物料查询",
                icon: "document",
              },
              {
                path: "/userUrl/ProductMatterSearch",
                label: "产品查询",
                icon: "document",
              },
            ],
          },
          {
            path: "/userUrl/PrintTable",
            label: "打印管理",
            icon: "document",
            children: [
           
              {
                path: "/userUrl/PrintCode",
                label: "打印条码",
                icon: "document",
              },
           
            ],
          },
        ];
      } else {
        this.asideMenu = [
          // {
          //   path: '/',
          //   label: '首页',
          //   icon: 'home'
          // },
          {
            path: "/UserList",
            label: "基础管理",
            icon: "document",
            children: [
              // {
              //   path: "/Database",
              //   label: "数据库列表",
              //   icon: "document",
              // },

              {
                path: "/ProjectBOM",
                label: "产品结构",
                icon: "document",
              },
              {
                path: "/WorkStationList",
                label: "工站列表",
                icon: "document",
              },
              {
                path: "/ProcessList",
                label: "工序管理",
                icon: "document",
              },
              {
                path: "/UserList",
                label: "人员列表",
                icon: "document",
              },
              {
                path: "/MaterialList",
                label: "物料清单",
                icon: "document",
              },
              // {
              //   path: "/CheckList",
              //   label: "检验管理",
              //   icon: "document",
              // },
              // {
              //   path: "/PackList",
              //   label: "包装管理",
              //   icon: "document",
              // },
            ],
          },
          {
            path: "/ProductionPlan",
            label: "车间管理",
            icon: "document",
            children: [
              {
                path: "/MaterialRequisition",
                label: "生产领料",
                icon: "document",
              },
              {
                path: "/MaterialReturn",
                label: "生产退料",
                icon: "document",
              },
              {
                path: "/ProductionPlan",
                label: "生产计划",
                icon: "document",
              },
            ],
          },
          {
            path: "/MatterProductSearch",
            label: "信息追溯",
            icon: "document",
            children: [
              // {
              //   path: "/WorkStationSearch",
              //   label: "过站查询",
              //   icon: "document",
              // },
              
              {
                path: "/MatterProductSearch",
                label: "物料查询",
                icon: "document",
              },
              {
                path: "/ProductMatterSearch",
                label: "产品查询",
                icon: "document",
              },
            ],
          },
          {
            path: "/PrintTable",
            label: "打印管理",
            icon: "document",
            children: [
              {
                path: "/FileTemplateList",
                label: "文件模板",
                icon: "document",
              },
              {
                path: "/PrintTable",
                label: "打印参数",
                icon: "document",
              },
            ],
          },
        ];
      }
    }
  },
};
</script>

<style scoped lang="scss">
.logo {
  height: 56px;
}
.logoImg {
  width: 100%;
  height: 100%;
  background: url("../assets/images/logoText.png") 100%;
  background-size: cover;
  cursor: pointer;
}
.logoIcon {
  // width: 100%;
  height: 56px;
  line-height: 56px;
  margin-left: 20px;
  i {
    vertical-align: middle;
    font-size: 40px;
  }
}
.el-menu {
  //height: 100%;
  border: 0;
}
</style>
